import React from "react";
import "./ShowMore.scss";
export default function Showmore() {
     const handleShowMore = (e) => {
          const showEvent = document.querySelector(".host__show");
          const showBtn = document.querySelector(".showmore");
          showEvent.style.display = "block";
          showBtn.style.display = "none";
     };
     return (
          <>
               <div onClick={handleShowMore} className="showmore">
                    <span className="showmore__text">
                         Xem các sự kiện đã kết thúc
                    </span>
                    <div className="showmore__btn">
                         <i class="fas fa-chevron-circle-down"></i>
                    </div>
               </div>
          </>
     );
}
