import React from "react";
import Hosting from "../components/Hosting/Hosting";
import IMG_LIST from "../components/LINK__IMG";
import Showmore from "../components/ShowMore/Showmore";
export default function HostingContain() {
     return (
          <div
               style={{
                    paddingTop: 50,
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "center",
                    alignItems: "center",
               }}
          >
               <Hosting
                    Info={IMG_LIST.hostInfo}
                    title="TẾT YÊU THƯƠNG"
                    sub="Chương trình 2021-2022"
                    active={true}
               />
               <Showmore />
               <div style={{ display: "none" }} className="host__show">
                    <Hosting
                         Info={IMG_LIST.hostInfo}
                         title="TẾT THIẾU NHI"
                         sub="Chương trình 2021-2022"
                    />
                    <Hosting
                         Info={IMG_LIST.hostInfo}
                         title="TẾT TRUNG THU"
                         sub="Chương trình 2021-2022"
                    />
                    <Hosting
                         Info={IMG_LIST.hostInfo}
                         title="ÁO ẤM MÙA ĐÔNG"
                         sub="Chương trình 2021-2022"
                    />
                    <Hosting
                         Info={IMG_LIST.hostInfo}
                         title="GIÁNG SINH ẤM ÁP"
                         sub="Chương trình 2021-2022"
                    />
               </div>
          </div>
     );
}
