import React from "react";
import MainSlider from "../components/MainSlider/MainSlider";
import { useEffect } from "react";
export default function SliderContain() {
     useEffect(() => {
          const BackBtn = document.querySelector(".header__back");
          const logo = document.querySelector(".header__fixlogo.none");
          BackBtn && BackBtn.classList.add("none");
          logo && logo.classList.remove("none");

          return () => {
               const BackBtnN = document.querySelector(".header__back.none");
               const logo = document.querySelector(".header__fixlogo");
               logo && logo.classList.add("none");
               BackBtnN && BackBtnN.classList.remove("none");
          };
     }, []);

     return (
          <>
               <MainSlider
                    title="ĐỘI TÌNH NGUYỆN MẦM XANH"
                    sub="Kết nối yêu thương - Ươm mầm mơ ước"
               />
          </>
     );
}
